.van-progress {
  position: relative;
  height: 0.17067rem;
  height: var(--progress-height, 0.17067rem);
  border-radius: 0.17067rem;
  border-radius: var(--progress-height, 0.17067rem);
  background: #ebedf0;
  background: var(--progress-background-color, #ebedf0);
}
.van-progress__portion {
  position: absolute;
  left: 0;
  height: 100%;
  border-radius: inherit;
  background: #1989fa;
  background: var(--progress-color, #1989fa);
}
.van-progress__pivot {
  position: absolute;
  top: 50%;
  box-sizing: border-box;
  min-width: 3.6em;
  text-align: center;
  word-break: keep-all;
  border-radius: 1em;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  color: #fff;
  color: var(--progress-pivot-text-color, #fff);
  padding: 0 0.21333rem;
  padding: var(--progress-pivot-padding, 0 0.21333rem);
  font-size: 0.42667rem;
  font-size: var(--progress-pivot-font-size, 0.42667rem);
  line-height: 1.6;
  line-height: var(--progress-pivot-line-height, 1.6);
  background-color: #1989fa;
  background-color: var(--progress-pivot-background-color, #1989fa);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
